/* GLOBAL VALUES */
/* LAYOUT OPTIONS */
.flexbox {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.flexbox-wrap {
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.flexbox-between {
  -webkit-box-pack: space-between;
  -moz-box-pack: space-between;
  -ms-flex-pack: space-between;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.flexbox-around {
  -webkit-box-pack: space-around;
  -moz-box-pack: space-around;
  -ms-flex-pack: space-around;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}
.fwidth {
  width: 100%;
}
.w80 {
  width: 80%;
}
.w33 {
  width: calc(100% / 3);
}
.w66 {
  width: calc((100% / 3) * 2);
}
.w75 {
  width: 75%;
}
.w50 {
  width: 50%;
}
.w25 {
  width: 25%;
}
.row {
  max-width: 1200px;
  margin: 0 auto;
}
.justify-center {
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}
.justify-around {
  -webkit-box-pack: space-around;
  -moz-box-pack: space-around;
  -ms-flex-pack: space-around;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}
.justify-between {
  -webkit-box-pack: space-between;
  -moz-box-pack: space-between;
  -ms-flex-pack: space-between;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.justify-start {
  -webkit-box-pack: flex-start;
  -moz-box-pack: flex-start;
  -ms-flex-pack: flex-start;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
}
/* IMAGERY */
.fill-background {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
/* FORMS AND INPUTS */
input[type="text"],
input[type="password"],
textarea {
  padding: 12px;
  box-sizing: border-box;
  border: 1px solid #d3d3d3;
  transition: all .33s ease;
  width: 100%;
  resize: none;
  font-weight: 300;
}
input[type="text"]:focus,
input[type="password"]:focus,
textarea:focus {
  outline: 0;
  border-color: #646464;
}
select {
  width: 100%;
  -webkit-appearance: none;
  border-radius: 0;
  background-color: transparent;
  border: 1px solid #d3d3d3;
  padding: 12px;
  outline: 0;
  font-size: 11px;
  letter-spacing: 1px;
  text-indent: calc(100% - 32px);
  cursor: pointer;
}
input[type="file"]:focus {
  outline: 0;
}
textarea {
  margin-bottom: -2px;
}
.block-button {
  padding: 14px 16px;
  background: #323232;
  color: #fdfdfd;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  box-sizing: border-box;
  transition: all .11s ease;
}
.block-button:hover {
  box-shadow: 0 2px 6px 3px rgba(0, 0, 0, 0.15);
  transform: translate3d(0, -2px, 0);
}
.loading-spinner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 32px;
  height: 32px;
  background-image: url('/matter/assets/images/spinner.png');
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  animation-name: spinner-rotate;
  animation-duration: 500ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
/* TEXT OPTIONS */
.to-lower {
  text-transform: lowercase;
  color: inherit;
}
/* ANIMATIONS */
@-webkit-keyframes spinner-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
.app-wrapper {
  height: 100vh;
}
.app-wrapper .sidebar {
  width: 240px;
  background: #323232;
  color: #fdfdfd;
  height: 100vh;
  text-align: right;
  position: relative;
}
.app-wrapper .sidebar ul {
  padding-top: 64px;
}
.app-wrapper .sidebar ul li h3 {
  margin: 0;
  padding: 16px;
  font-weight: 100;
  font-size: 22px;
}
.app-wrapper .sidebar ul li a {
  color: #fdfdfd;
  padding: 16px;
  display: block;
  text-transform: uppercase;
  transition: all .33s ease;
  font-weight: 200;
  font-size: 14px;
  letter-spacing: 1px;
}
.app-wrapper .sidebar ul li a:hover {
  transform: translate3d(-8px, 0, 0);
}
.app-wrapper .sidebar #logout {
  color: #F44336;
  padding: 16px;
  display: block;
  text-transform: uppercase;
  transition: all .33s ease;
  font-weight: 200;
  font-size: 14px;
  letter-spacing: 1px;
  position: absolute;
  right: 0;
  bottom: 32px;
}
.app-wrapper .sidebar #logout:hover {
  transform: translate3d(-8px, 0, 0);
}
.app-wrapper .app-content {
  width: calc(100% - 240px);
  height: 100vh;
  color: #323232;
  position: relative;
  padding: 32px;
  box-sizing: border-box;
}
.app-wrapper .app-content .content-header {
  padding: 30px 0 8px 0;
  margin-bottom: 16px;
  border-bottom: 1px solid #d3d3d3;
}
.app-wrapper .app-content .content-header .content-title {
  font-size: 20px;
  font-weight: 200;
  text-transform: uppercase;
  margin-bottom: 0;
  letter-spacing: 2px;
}
.app-wrapper .app-content .content-header .content-action-button {
  font-size: 12px;
  letter-spacing: 2px;
  background: #323232;
  color: #323232;
  text-transform: uppercase;
  height: 40px;
  padding: 0 20px;
  line-height: 40px;
  color: #fdfdfd;
}
.app-wrapper .app-content .content-wrapper {
  max-height: calc(100vh - 172px);
  overflow-y: scroll;
  padding-bottom: 16px;
}
.app-wrapper #monolog {
  position: fixed;
  right: 24px;
  bottom: 16px;
  width: 240px;
  background: white;
  padding: 16px;
  box-shadow: 0 0 6px 3px rgba(0, 0, 0, 0.05);
  opacity: 0;
  visibility: hidden;
  transition: all .33s ease;
}
.app-wrapper #monolog #mono-title {
  margin: 0;
  font-size: 13px;
  margin-bottom: 8px;
  border-bottom: 1px solid #d3d3d3;
  padding-bottom: 8px;
}
.app-wrapper #monolog #mono-msg {
  margin: 0;
  font-weight: 300;
}
.app-wrapper #monolog.active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, -8px, 0);
}
.modal-wrapper {
  position: fixed;
  background: rgba(0, 0, 0, 0.35);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: all .33s ease;
}
.modal-wrapper .modal-window {
  position: absolute;
  background: white;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 90vw;
  height: 90vh;
  max-height: 640px;
  max-width: 860px;
  transition: all .33s ease;
  padding: 24px;
  box-sizing: border-box;
  transform: translate3d(0, -16px, 0);
}
.modal-wrapper .modal-window .modal-close {
  position: absolute;
  right: 12px;
  top: 12px;
  color: #323232;
}
.modal-wrapper.active {
  visibility: visible;
  opacity: 1;
}
.modal-wrapper.active .modal-window {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.page-dashboard .navigation-wrapper .navigation-group .navigation-item {
  padding: 16px;
  min-width: 200px;
  position: relative;
}
.page-dashboard .navigation-wrapper .navigation-group .navigation-item:hover {
  background: #2196F3;
  color: #fdfdfd;
}
.page-dashboard .navigation-wrapper .navigation-group .navigation-item:hover:after {
  content: "";
  display: block;
  width: 28px;
  height: 28px;
  position: absolute;
  right: -6px;
  background: #2196F3;
  transform: rotate(45deg);
  top: 10px;
}
.page-navigation .navigation-wrapper .navigation-group .navigation-item {
  padding: 16px;
  cursor: pointer;
  min-width: 200px;
  position: relative;
  border-right: 1px solid #d3d3d3;
  transition: background .33s ease;
}
.page-navigation .navigation-wrapper .navigation-group .navigation-item:hover {
  background: rgba(0, 0, 0, 0.05);
}
.page-navigation .navigation-wrapper .navigation-group .navigation-item i {
  top: 0;
  bottom: 0;
  right: -14px;
  height: 24px;
  margin: auto;
  display: none;
