}
#blog-add {
  max-width: 640px;
  margin: 0 auto;
  background: white;
  border-radius: 4px;
  box-shadow: 0 0 12px 6px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  text-align: right;
  margin-top: 16px;
}
#blog-add #expandable-textarea {
  overflow: hidden;
  transition: all .33s ease;
  max-height: 481px;
  transform: translate3d(0, 0, 0);
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
#blog-add #expandable-textarea #picture-wrapper {
  position: relative;
  width: 100%;
}
#blog-add #expandable-textarea #picture-wrapper .picture-preview {
  width: 100%;
  height: 240px;
  border-bottom: 1px solid #d3d3d3;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
#blog-add #expandable-textarea #picture-wrapper .picture-preview p {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  line-height: 164px;
  border: 2px dashed #d3d3d3;
  text-align: center;
  color: #d3d3d3;
  font-weight: 100;
  font-size: 24px;
  width: calc(100% - 64px);
  height: calc(100% - 64px);
}
#blog-add #expandable-textarea #picture-wrapper input {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
}
#blog-add #expandable-textarea.compress {
  max-height: 0;
}
#blog-add #expandable-textarea.compress ~ .textarea .mce-edit-area {
  height: calc(100vh - 340px);
  transition: all .33s ease;
}
#blog-add .input-group.head {
  padding: 8px 0;
}
#blog-add .input-group.head > .input-group {
  -webkit-flex-wrap: wrap;
  -moz-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
#blog-add .input-group.head > .input-group .input-wrapper {
  width: 100%;
  padding: 8px 0;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
#blog-add .input-group.head > .input-group .input-wrapper p {
  text-align: left;
  width: 30%;
  text-indent: 24px;
}
#blog-add .input-group.head > .input-group .input-wrapper input {
  border: 0;
  background: #fdfdfd;
  width: 66%;
}
#blog-add #add-blog-submit {
  -webkit-appearance: none;
