// out: null

.app-wrapper{
	height: 100vh;

	.sidebar{
		width: 240px;
		background: #323232;
		color: #fdfdfd;
		height: 100vh;
		text-align: right;
		position: relative;

		ul{
			padding-top: 64px;

			li{

				h3{
					margin: 0;
					padding: 16px;
					font-weight: 100;
					font-size: 22px;
				}

				a{
					color: #fdfdfd;
					padding: 16px;
					display: block;
					text-transform: uppercase;
					transition: all .33s ease;
					font-weight: 200;
					font-size: 14px;
					letter-spacing: 1px;

					&:hover{
						transform: translate3d(-8px, 0, 0);
					}
				}
			}
		}

		#logout{
			color: @gred;
			padding: 16px;
			display: block;
			text-transform: uppercase;
			transition: all .33s ease;
			font-weight: 200;
			font-size: 14px;
			letter-spacing: 1px;
			position: absolute;
			right: 0;
			bottom: 32px;

			&:hover{
				transform: translate3d(-8px, 0, 0);
			}
		}
	}

	.app-content{
		width: calc(~"100% - 240px");
		height: 100vh;
		color: #323232;
		position: relative;
		padding: 32px;
		box-sizing: border-box;

		.content-header{
			padding: 30px 0 8px 0;
			margin-bottom: 16px;
			border-bottom: 1px solid #d3d3d3;

			.content-title{
				font-size: 20px;
				font-weight: 200;
				text-transform: uppercase;
				margin-bottom: 0;
				letter-spacing: 2px;
			}

			.content-action-button{
				font-size: 12px;
				letter-spacing: 2px;
				background: #323232;
				color: #323232;
				text-transform: uppercase;
				height: 40px;
				padding: 0 20px;
				line-height: 40px;
				color: #fdfdfd;
			}
		}

		.content-wrapper{
			max-height: calc(~"100vh - 172px");
			overflow-y: scroll;
			padding-bottom: 16px;
		}
	}
	
	#monolog{
		position: fixed;
		right: 24px;
		bottom: 16px;
		width: 240px;
		background: white;
		padding: 16px;
		box-shadow: 0 0 6px 3px rgba(0, 0, 0, .05);
		opacity: 0;
		visibility: hidden;
		transition: all .33s ease;
		
		#mono-title{
			margin: 0;
			font-size: 13px;
			margin-bottom: 8px;
			border-bottom: 1px solid #d3d3d3;
			padding-bottom: 8px;
		}
		
		#mono-msg{
			margin: 0;
			font-weight: 300;
		}
		
		&.active{
			opacity: 1;
			visibility: visible;
			transform: translate3d(0, -8px, 0);
		}
	}
}