.modal-wrapper{
	position: fixed;
	background: rgba(0, 0, 0, .35);
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	visibility: hidden;
	opacity: 0;
	transition: all .33s ease;

	.modal-window{
		position: absolute;
		background: white;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		width: 90vw;
		height: 90vh;
		max-height: 640px;
		max-width: 860px;
		transition: all .33s ease;
		padding: 24px;
		box-sizing: border-box;
		transform: translate3d(0, -16px, 0);

		.modal-close{
			position: absolute;
			right: 12px;
			top: 12px;
			color: #323232;

			i{

			}
		}
	}

	&.active{
		visibility: visible;
		opacity: 1;

		.modal-window{
			opacity: 1;
			transform: translate3d(0, 0, 0);
		}
	}
}