// out: null

.page-navigation{

	.navigation-wrapper{

		.navigation-group{

			.navigation-item{
				padding: 16px;
				cursor: pointer;
				min-width: 200px;
				position: relative;
				border-right: 1px solid #d3d3d3;
				transition: background .33s ease;

				&:hover{
					background: rgba(0, 0, 0, .05);
				}
				
				i{
					top: 0;
					bottom: 0;
					right: -14px;
					height: 24px;
					margin: auto;
					display: none;
					color: #323232;
					position: absolute;
				}
				
				&-main{

					&.active{
						color: white;
						background: #323232;
						
						i{
							display: block;
						}
					}
				}
			}
			
			.add-cat-button{
				position: relative;
				
				a{
					color: #323232;
					display: block;
					text-indent: 24px;
					padding: 10px 16px;
					box-sizing: border-box;
					border: 2px solid #323232;
					
					i{
						top: 0;
						bottom: 0;
						left: -10px;
						margin: auto;
						height: 24px;
						position: absolute;
					}
				}
			}

			&-main{
			}

			&-grand{

			}
		}

		.navigation-group-sub-wrapper{
			display: none;
			
			&.active{
				display: block;
			}
		}

		.navigation-group-grand-wrapper{
			display: none;
			
			&.active{
				display: block;
			}
		}
	}
}