/* GLOBAL VALUES */

/* LAYOUT OPTIONS */
.flexbox{
	.flex-block();

	&-wrap{
		.flex-wrap(wrap);
	}

	&-between{
		.justify-content(space-between);
	}

	&-around{
		.justify-content(space-around);
	}
}

.fwidth{
	width: 100%;
}

.w80{
	width: 80%;
}

.w33{
	width: calc(~"100% / 3");
}

.w66{
	width: calc(~"(100% / 3) * 2");
}

.w75{
	width: 75%;
}

.w50{
	width: 50%;
}

.w25{
	width: 25%;
}

.row{
	max-width: 1200px;
	margin: 0 auto;
}

.justify{

	&-center{
		.justify-content(center);
	}

	&-around{
		.justify-content(space-around);
	}

	&-between{
		.justify-content(space-between);
	}
	
	&-start{
		.justify-content(flex-start);
	}
}

/* IMAGERY */
.fill-background{
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

/* FORMS AND INPUTS */
input[type="text"], input[type="password"], textarea{
	padding: 12px;
	box-sizing: border-box;
	border: 1px solid #d3d3d3;
	transition: all .33s ease;
	width: 100%;
	resize: none;
	font-weight: 300;

	&:focus{
		outline: 0;
		border-color: #646464;
	}
}

select{
	width: 100%;
	-webkit-appearance: none;
	border-radius: 0;
	background-color: transparent;
	border: 1px solid #d3d3d3;
	padding: 12px;
	outline: 0;
	font-size: 11px;
	letter-spacing: 1px;
	text-indent: calc(~"100% - 32px");
	cursor: pointer;
}

input[type="file"]{
	&:focus{
		outline: 0;
	}
}

textarea{
	margin-bottom: -2px;
}

.block-button{
	padding: 14px 16px;
	background: #323232;
	color: #fdfdfd;
	font-size: 12px;
	letter-spacing: 1px;
	text-transform: uppercase;
	box-sizing: border-box;
	transition: all .11s ease;

	&:hover{
		box-shadow: 0 2px 6px 3px rgba(0, 0, 0, .15);
		transform: translate3d(0, -2px, 0);
	}
}

.loading-spinner{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	width: 32px;
	height: 32px;
	background-image: url('/matter/assets/images/spinner.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center;
	animation-name: spinner-rotate;
	animation-duration: 500ms;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}

/* TEXT OPTIONS */
.to-lower{
	text-transform: lowercase;
	color: inherit;
}

/* ANIMATIONS */
@-webkit-keyframes spinner-rotate{
	0%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(359deg);
	}
}