.page-dashboard{

	.navigation-wrapper{

		.navigation-group{

			.navigation-item{
				padding: 16px;
				min-width: 200px;
				position: relative;

				&:hover{
					background: #2196F3;
					color: #fdfdfd;

					&:after{
						content: "";
						display: block;
						width: 28px;
						height: 28px;
						position: absolute;
						right: -6px;
						background: #2196F3;
						transform: rotate(45deg);
						top: 10px;
					}
				}
			}

			&-main{

			}

			&-grand{

			}
		}
	}
}