// out:null
.code-editor-wrapper{
	
	.file-browser{
		width: 200px;
		
		.file-item{
			display: block;
			padding: 8px;
			font-weight: 300;
			color: #323232;
		}
		
		.file-add{
			color: #323232;
			position: relative;
			display: block;
			padding: 8px;
			text-transform: uppercase;
			
			i{
				color: #323232;
				position: absolute;
				top: 0;
				bottom: 0;
				right: 8px;
				margin: auto;
				height: 24px;
			}
		}
	}
	
	#code-mirror{
		width: calc(~"100% - 200px");
		height: calc(~"100vh - 156px");
		background-color: #f1f1f1;
		border-top-left-radius: 4px;
		border-top-right-radius: 4px;
		position: relative;
		
		#code-file-name{
			font-family: monospace;
			position: absolute;
			margin: 0;
			padding: 0;
			line-height: 40px;
			left: 42px;
			font-weight: 700;
		}
		
		.CodeMirror{
			height: calc(~"100% - 40px");
			margin-top: 40px;
			border-top: 1px solid rgba(255, 255, 255, .1);
		}
		
		*{
			font-family: monospace;
		}
	}
}